import {
    Flow_ChangeLang,
} from '../types';
import us from '../../assets/media/us.svg'

const initialState = {
    language:"english",
    img: us
}

const FlowReducer = (state = initialState, action) => {
    switch(action.type){
        case Flow_ChangeLang:
            return Object.assign({},state, {language:action.lang});
        default:
            return state;
    }
}

export default FlowReducer