import React, { useState, useEffect } from 'react';
import {Helmet} from "react-helmet";
import Topbar from '../Topbar';
import Cart from './Cart';
import {Categories, brands} from '../Constants'
import {FaCartPlus, FaSearchengin, FaCartArrowDown} from 'react-icons/fa';
import {BsFillInfoCircleFill, BsFillCaretDownFill, BsFillCaretUpFill} from 'react-icons/bs';
import {AiOutlineCloseCircle} from 'react-icons/ai';
import {GiShop} from 'react-icons/gi';
import dg from '../assets/dg.jpg';
import dpg from '../assets/dpg.jpg';
import tl from '../assets/logos/tl.svg'
import { mainstrings } from '../Strings';
import Waiting from '../Waiting/index.js';
import ad from '../assets/ad.svg'
import './styles.css'



export default function Shop() {
    const [isMob, setIsMob] = useState(false)
    const [didFetch, setDidFetch] = useState(false);
    const [shopItems, setShopItems] = useState([]);
    const [category, setCategory] = useState('All');
    const [brand, setBrand] = useState('');
    const [displayList, setDisplayList] = useState(false);
    const [displayModal, setDisplayModal] = useState(false);
    const [displayCart, setDisplayCart] = useState(false);
    const [logged, setLogged] = useState(false);
    const [mobileMenu, setMobileMenu] = useState(false);
    const [searchValue, setSearchValue] = useState("")

    useEffect(() => {
        if (!didFetch){
            fetchCategory(category)
            setDidFetch(true);
        }
        (function(a){if(/(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows ce|xda|xiino/i.test(a)||/1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s\-)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|\-m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw\-(n|u)|c55\/|capi|ccwa|cdm\-|cell|chtm|cldc|cmd\-|co(mp|nd)|craw|da(it|ll|ng)|dbte|dc\-s|devi|dica|dmob|do(c|p)o|ds(12|\-d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(\-|)|g1 u|g560|gene|gf\-5|g\-mo|go(\.w|od)|gr(ad|un)|haie|hcit|hd\-(m|p|t)|hei\-|hi(pt|ta)|hp( i|ip)|hs\-c|ht(c(\-| ||a|g|p|s|t)|tp)|hu(aw|tc)|i\-(20|go|ma)|i230|iac( |\-|\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\/)|klon|kpt |kwc\-|kyo(c|k)|le(no|xi)|lg( g|\/(k|l|u)|50|54|\-[a-w])|libw|lynx|m1\-w|m3ga|m50\/|ma(te|ui|xo)|mc(01|21|ca)|m\-cr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(\-| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)\-|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|\-([1-8]|c))|phil|pire|pl(ay|uc)|pn\-2|po(ck|rt|se)|prox|psio|pt\-g|qa\-a|qc(07|12|21|32|60|\-[2-7]|i\-)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h\-|oo|p\-)|sdk\/|se(c(\-|0|1)|47|mc|nd|ri)|sgh\-|shar|sie(\-|m)|sk\-0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h\-|v\-|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl\-|tdg\-|tel(i|m)|tim\-|t\-mo|to(pl|sh)|ts(70|m\-|m3|m5)|tx\-9|up(\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|\-v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(\-| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas\-|your|zeto|zte\-/i.test(a.substr(0,4))) setIsMob(true);})(navigator.userAgent||navigator.vendor||window.opera);
    },[isMob]);
   
    const fetchCategory = async (categ) => {
        await fetch('https://tazweid.asp-api.com/api/v1/hg4itvufehtqw7ine4cnm5h/resources/parts/'.concat(categ !== null ? categ : 'all'), {
            'credentials':'include'
        })
        .then(res => {if (res.status === 200){
            return res.json();
        }else{
            return null
        }}
        ).then(jRes => {
            try{
                setShopItems(jRes)
            }
            catch{
                setShopItems([])
            }
            })
        .catch(err =>  {
            return null;
        })
    };

    const handleOptions = (typ) => {
        switch(typ){
            case "Bearings":
                return(
                    <ul className="measurments">
                        <label>inner<input type="number" name="name"/></label><span>X</span>
                        <label>outer<input type="number" name="name"/></label><span>X</span>
                        <label>thikness<input type="number" name="name"/></label>
                    </ul>
                );
            case "Belts":
                return(
                    <ul className="measurments">
                    <label>thikness<input type="number" name="name"/></label><span></span>
                    <label>length<input type="number" name="name"/></label>
                </ul> 
                );
            default:
                return(
                    <h4>No Measurments For This Choice</h4>
                )
        }
    };
    const SideITem = () => {
        return(
            <div className={isMob ? "side-item mob" : "side-item"}>
                 <h3 onClick={() => setDisplayList(!displayList)}>
                    Categories
                    <span>{displayList? <BsFillCaretUpFill/> : <BsFillCaretDownFill/>}</span>
                </h3>
                <ul className={displayList? "showList" : 'showList dis'} onClick={(e) => catClick(e)}>
                    {Categories.map((i) => <li className={category === i.title ? "active" : ''}>{i.title}</li>)}
                </ul>

                {category === "Select Category" ? 
                    <></>
                     :
                      <>
                        <h3>Options</h3>
                        {handleOptions(category)}
                        </>
                }
                
                {displayCart ? 
                   <h3 onClick={() => {setDisplayCart(false); setMobileMenu(false)}}>Shop<span><GiShop/></span></h3>
                    :
                    <h3 onClick={() => {setDisplayCart(true);setMobileMenu(false)}}> My Cart<span><FaCartArrowDown/></span></h3>
                }
                
            </div>
        )
    };
    const ProductsCard = () => {
        return(
            <>
            <h1>{category}</h1>
            <div className={isMob ? "card-holder mob" : "card-holder"}>
                {shopItems.length == 0 ? console.log("No Data"): shopItems.map((product) =>
                    <div className="card-product">
                        <div className="card-head">
                            <i onClick={() => setDisplayModal(true)}><BsFillInfoCircleFill/></i>
                            <h2>{product.partnumber}</h2>
                        </div>
                            <img src="https://tazweid.asp-api.com/api/v1/hg4itvufehtqw7ine4cnm5h/resources/img/bearings/ball"/>
                        <div className="category">
                                <h3>{mainstrings("Category")}: <span>{product.category}</span></h3>
                                {/* <h3>{mainstrings("Sub_Category")}: <span>{product.sub_cat}</span></h3> */}
                            <div className="aligning">
                                <h3>{mainstrings("Brand")}: <img src={tl}/></h3>
                                {product.hasOwnProperty('price') ? <h3>{mainstrings("Warehouse")}: <img src={findWarehouseFromRef(product.ref)}/></h3> : <></>}
                            </div>
                            {product.hasOwnProperty('price') ?
                                <div className="aligning">
                                    <h3>{mainstrings("Quantity")}:<span>{product.quantity}</span></h3>
                                    <h3>{mainstrings("Price")}: <span>{product.price}</span></h3> 
                                    </div>
                                    :
                                <div className="aligning">
                                    <h3>{mainstrings("Quantity")}:<input type="number" placeholder="00"/></h3>
                                    <h3 className="category cart">Add To Cart<i><FaCartPlus/></i></h3>
                                </div>
                            }
                        </div>
                    </div>
                )}
            </div>
            </>
                );
    };
    const ModalDesc = () => {
        return(
            <div className="modal-section">
                <input className="modal-btn" checked={displayModal} type="checkbox" />
                <i id="closeModal" onClick={() => setDisplayModal(false)}><AiOutlineCloseCircle/></i>
                <div className="modal">		
                    <div className="modal-wrap">	
                        <img src={dpg} alt="No image for this item"/>	
                        <p>Some Description About Item</p>	          		
                    </div>			          		
                </div>	
            </div>
        )
    };

    const catClick = (e) => {
        setCategory(e.target.textContent);
        setDidFetch(false)
         
        if(displayCart){
            setDisplayCart(false)
        }
        setMobileMenu(false)
    };
    
    const findWarehouseFromRef = (id) => {
       if (id.includes('timken')){
            return 'https://tazweid.asp-api.com/api/v1/hg4itvufehtqw7ine4cnm5h/resources/img/brands/timken'
       }else if (id.includes('skf')){
            return 'https://tazweid.asp-api.com/api/v1/hg4itvufehtqw7ine4cnm5h/resources/img/brands/skf'
       }else if (id.includes('pioneers')){
            return 'https://tazweid.asp-api.com/api/v1/hg4itvufehtqw7ine4cnm5h/resources/img/brands/pioneers'
       }else if (id.includes('tazweid')){
            return 'https://tazweid.asp-api.com/api/v1/hg4itvufehtqw7ine4cnm5h/resources/img/brands/tazweid'
       }else{
           return 'https://tazweid.asp-api.com/api/v1/hg4itvufehtqw7ine4cnm5h/resources/img/brands/asp'
       }
    };
    const fetchSearch = async () => {
        if (searchValue == ""){
            return null;
        }else{
            var formdata = new FormData();
            formdata.append('partnumber', searchValue);
        }

        await fetch('https://tazweid.asp-api.com/api/v1/hg4itvufehtqw7ine4cnm5h/search/resources/parts',{
            credentials:'include',
            method:"POST",
            body:formdata
        })
        .then(res => {
            if (res.status === 200){
                return res.json()
            } else {
                
                return ;
            }
          }
        ).then(jRes => {
            try{
                if (jRes.length > 0){ 
                    setShopItems(jRes);
                    setCategory("Search")
                }
            }catch{
                console.log("No Data")
            }
          }
        ).catch(err => console.log(err))
        setDidFetch(true);
    };

    return(
        <div>
            <Topbar/>
            {/* <Waiting/> */}
            <div>
                <div className={isMob ? "search-cont mob" : "search-cont"}  style={{height: isMob && mobileMenu ? "100%" : "", zIndex: isMob && mobileMenu ? 3 : ""}}> 
                <div class="seacrhBox">
                    <div class="seacrhBox-inner">
                        <label>Search for your requirement</label>
                        <div class="seacrhBox-container">
                            
                            <div class="seacrhBox-inputContainer">
                                <input type="text" placeholder="00 X 00 X 00...." onChange={(e) => setSearchValue(e.target.value)}/>
                            </div>
                            <div class="seacrhBox-icon" onClick={() => fetchSearch()}>
                                <i><FaSearchengin/></i>
                            </div>
                        </div>
                    </div>
            </div>
                        {isMob && !mobileMenu ? <button className="arrowDown" onClick={() => setMobileMenu(true)} style={{top:mobileMenu ? "80vh" : ""}}><img src={ad}/></button> : <></>}
                    <SideITem/>

                </div>
            <div className={isMob ? "shop-body mob" : "shop-body"}>
                {displayCart ?  <Cart/> : <ProductsCard/>}
                    <ModalDesc/>

            </div>

        </div>
           </div>

           
    );
};

