export const mainstrings = (string,state) => {
    const currentLang =  localStorage.getItem('language');

    try{
        switch(currentLang.toLowerCase()){
            case "english":
                return english[string];
            case "arabic":
                return arabic[string];
            default:
                return english[string];
        }
    }
    catch{
        return english[string]
    }
};